
import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import selectCity from '@/components/SelectCity'
export default {
  name: 'offerDetail',
  data () {
    return {
      loading: false,
      company: {},
      orderUser: {
        license: '',
        name: '',
        tel: ''
      }
    }
  },
  components: {
    Group,
    XAddress,
    Cell,
    XButton,
    XImg,
    selectCity,
    XInput
  },
  created () {
    // 获取保险公司信息
    this.company = JSON.parse(this.$localStorage.get('orderCompany'))
    if (this.$route.params.userId === 'null') {
      this.$vux.toast.show({
        type: 'text',
        width: '20em',
        position: 'bottom',
        text: '无法继续操作，请返回登录！',
        time: '1000'
      })
    }
    // if (this.$localStorage.get('logined') !== 'true') {
    //   this.$vux.toast.show({
    //     type: 'text',
    //     width: '20em',
    //     position: 'bottom',
    //     text: '您尚未登录，请前往登录！',
    //     time: '1000'
    //   })
    //   this.$router.push('/login')
    // }
  },
  computed: {
    ...mapGetters({
      offer: 'getOffer',
      InsuranceArea: 'getInsuranceArea',
      checkAuthor: 'checkAuthor'
    })
  },
  methods: {
    handleSubmit () {
      this.loading = true
      if (this.$route.params.userId === 'null') {
        this.$vux.toast.show({
          type: 'text',
          width: '10em',
          position: 'bottom',
          text: '请登录！',
          time: '1000'
        })
        this.loading = false
      } else if (!this.InsuranceArea) {
        this.$vux.toast.show({
          type: 'text',
          width: '15em',
          position: 'bottom',
          text: '请填写所在地区！',
          time: '1000'
        })
        this.loading = false
      } else if (!this.$refs.license.valid || !this.$refs.name.valid || !this.$refs.tel.valid) {
        this.$vux.toast.show({
          type: 'text',
          width: '15em',
          position: 'bottom',
          text: '请如实填写以上信息！',
          time: '1000'
        })
        this.loading = false
      } else {
        // 存储用户订单信息到localstorage
        this.$localStorage.set('orderUser', JSON.stringify({
          insuranceArea: this.InsuranceArea,
          ownerName: this.orderUser.name,
          ownerLicense: this.orderUser.license,
          ownerTel: this.orderUser.tel
        }))
        this.loading = false
        this.$router.push('/offer/photograph/' + this.$route.params.id + '/' + this.$route.params.userId)
      }
    }
  }
}



